import React from "react";
import Navbar from "@/components/Navbar";
import Footer from "@/components/Footer";
import Image from "next/image";
import { Article } from "@/types";
import { notFound } from "next/navigation";

// Fetch Data
async function getArticleDetail(id: string): Promise<Article | null> {
  const baseUrl = process.env.NEXT_PUBLIC_BASE_URL ?? "http://localhost:3000";
  try {
    const res = await fetch(`${baseUrl}/api/article/${id}`, {
      cache: "no-store",
    });
    if (!res.ok) return null;
    return res.json();
  } catch (error) {
    return null;
  }
}

export default async function ArticleDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;
  const article = await getArticleDetail(id);

  if (!article) {
    notFound();
  }

  return (
    <main className="bg-black min-h-screen text-white selection:bg-purple-500 selection:text-white">
      <Navbar />

      {/* CONTAINER UTAMA */}
      {/* pt-32 memberikan jarak dari atas agar tidak tertutup Navbar */}
      <div className="max-w-4xl mx-auto px-6 pt-32 pb-20">
        
        {/* 1. GAMBAR THUMBNAIL (Jelas & Di atas Judul) */}
        <div className="relative w-full aspect-video rounded-2xl overflow-hidden border border-white/10 shadow-2xl mb-10">
          <Image
            src={article.image}
            alt={article.title}
            fill
            className="object-cover" // Hapus opacity agar gambar jelas
            priority
          />
        </div>

        {/* 2. HEADER: Metadata & Judul */}
        <div className="mb-12 border-b border-white/10 pb-10">
          {/* Metadata (Kategori & Tanggal) */}
          <div className="flex items-center gap-4 mb-6 text-sm font-bold tracking-widest uppercase text-gray-400">
            <span className="bg-blue-600 text-white px-3 py-1 rounded-md">
              Teknologi
            </span>
            <span>•</span>
            <span suppressHydrationWarning>
              {new Date(article.createdAt).toLocaleDateString("id-ID", {
                day: "numeric",
                month: "long",
                year: "numeric",
              })}
            </span>
          </div>

          {/* Judul Artikel */}
          <h1 className="font-heading text-4xl md:text-6xl font-bold leading-tight text-white">
            {article.title}
          </h1>
        </div>

        {/* 3. ISI KONTEN */}
        <article>
          <div
            className="prose prose-invert prose-lg text-gray-300 leading-relaxed max-w-none 
                /* Style untuk Heading di dalam konten */
                [&>h1]:text-3xl [&>h1]:font-bold [&>h1]:mb-4 [&>h1]:mt-8 [&_h1]:bg-transparent!
                [&>h2]:text-2xl [&>h2]:font-bold [&>h2]:mt-8 [&>h2]:mb-4 [&_h2]:bg-transparent!
                
                /* Style untuk Paragraf & List */
                [&>p]:mb-6 [&_p]:bg-transparent!
                [&>ul]:list-disc [&>ul]:pl-5 [&>ul]:mb-6
                [&>ol]:list-decimal [&>ol]:pl-5 [&>ol]:mb-6
                
                /* Style Pembersih Background Putih (PENTING) */
                [&>span]:bg-transparent! [&>span]:text-gray-300!
                [&_span]:bg-transparent! [&_span]:text-gray-300!"
            dangerouslySetInnerHTML={{ __html: article.content }}
          />
        </article>

        {/* Tombol Back */}
        <div className="mt-20 pt-10 border-t border-white/10">
          <a
            href="/article"
            className="inline-flex items-center gap-2 text-gray-400 hover:text-white transition-colors group"
          >
            <span className="group-hover:-translate-x-1 transition-transform">←</span> 
            Kembali ke Arsip Artikel
          </a>
        </div>
      </div>

      <Footer />
    </main>
  );
}